//-------------------CTA----------------------
.cta {
  position: fixed;
  z-index: 10;
  bottom: 1vh;
  right: 1%;

  @include for-phone-only {
    right: 5%;
  }
  &__bubble {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 1.5rem;
    border-radius: 50%;
    background: rgba($color-secondary, $alpha: 0.4);
    cursor: pointer;
    button {
      svg {
        fill: $color-primary;
        /*   @include for-desktop-up {
          fill: red;
        }
        @include for-tablet-landscape-up {
          fill: green;
        }
        @include for-tablet-portrait-up {
          fill: blue;
        }
        @include for-phone-only {
          fill: purple;
        } */
      }
    }
    &::before {
      content: "";
      @include absolute;
      opacity: 0;
      transition: 0.3s ease-in-out;
      z-index: -1;
      background: rgba($color-primary, $alpha: 0.4);
      border-radius: 50%;
    }
    &:hover::before {
      opacity: 1;
    }
    button {
      display: flex;
      align-items: center;
      justify-content: center;
      color: $color-primary;
    }
  }
  .chevron {
    @include for-phone-only {
      position: fixed;
      bottom: 1vh;
      right: 5%;
      z-index: 20;
    }
  }
  //-------------------CTA OPENED----------------------
  &__opened {
    position: fixed;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    width: 37rem;
    /*  height: clamp(40rem, 83.8vh, 75.8rem); */
    border-radius: 1.4rem;
    border: solid 0.1rem $color-primary;
    z-index: 10;
    bottom: 9.4rem;
    padding: clamp(1rem, 2.21vh, 2rem);
    right: 1.8rem;
    background: rgba($bg-primary, $alpha: 1);
    @include for-phone-only {
      padding: 0.5rem;
      left: 50%;
      right: 0;
      bottom: 0;
      transform: translate(-50%, 0);
    }
    &__welcome {
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 33rem;
      height: clamp(50rem, 71.5vh, 64.3rem);
      @include for-phone-only {
        @include responsive-custom-max-height(585px) {
          height: clamp(37rem, 71.5vh, 64.3rem);
        }
      }
      &__introduce {
        display: flex;
        flex-direction: column;
        width: 33rem;
        align-items: center;
        border-bottom: solid 0.1rem #fdfdfd;
      }
      &__title {
        text-align: center;
        font-size: map-get($font-size, medium);
      }
      &__p {
        margin-top: clamp(1.5rem, 3.3vh, 3rem);
        padding: 0 1.5rem;
        font-size: map-get($font-size, normal);
        font-weight: 900;
        &__mobile {
          padding: 2rem 1.5rem;
          font-size: map-get($font-size, normal);
          font-weight: 900;
          @include responsive-custom-max-height(585px) {
          padding: 0.8rem 1.5rem;
          }
        }
      }
      ul {
        padding: clamp(1rem, 2.3vh, 2rem) 0;
        li {
          margin-left: 1.5rem;
          padding-left: 1rem;
          list-style-type: disc;
          font-weight: 900;
          &:last-child {
            margin-top: 0.6rem;
          }
        }
      }
      svg[width="266"] {
        margin: 2rem 0;
        rect {
          display: inline;
          fill: #fff;
          stroke-width: 1;
        }
        path {
          display: inline;
          fill: #000;
          stroke-width: 1;
        }
      }
      h3 {
        margin-top: 3.6rem;
        font-size: map-get($font-size, middle);
        font-weight: 900;
      }
      img {
        margin: 2rem 0;
        max-width: 50%;
      }
      form {
        display: flex;
        flex-direction: column;
        gap: clamp(1rem, 2.3vh, 2rem);
        width: 30rem;
        /*   height: clamp(20rem, 56.2vh, 45.5rem); */
        margin: clamp(1rem, 2.3vh, 2rem) 0;
        font-family: $font-secondary, $font-default;
        @include for-phone-only {
          gap: 2rem;
          margin: 2rem 1rem;
          @include responsive-custom-max-height(585px) {
            gap: 1rem;
            margin: 1rem 1rem;
          }
        }
        &:valid {
          & > .cta__opened__welcome__submit > button {
            background: $bg-active;
            font-weight: 900;
            &:hover {
              background: $bg-submit;
            }
          }
        }
        & input[type="email"],
        select,
        textarea,
        button {
          border-radius: 1rem;
        }
        & input[type="email"],
        textarea {
          &:valid {
            background: $color-valid;
          }
        }
        label {
          align-self: flex-start;
          font-size: map-get($font-size, normal);
        }
        input {
          width: 30rem;
          height: clamp(3.5rem, 4.6vh, 4.1rem);
          padding: clamp(0.5rem, 1.1vh, 1rem) 1.6rem clamp(0.5rem, 1.1vh, 1rem)
            4.5rem;
          background-color: $color-primary;
        }
      }
      //-------------------REQUEST-SELECT----------------------
      &__with-selection {
        position: relative;
        display: flex;
        flex-direction: column;
        border-radius: 1rem;
        cursor: pointer;
        background: $bg-input;
        &::after {
          content: "";
          position: absolute;
          display: block;
          top: 50%;
          transform: translateY(-50%);
          right: 5%;
          width: 2rem;
          height: 2rem;
          background-image: url("../assets/img/contact/chevron-bas.svg");
          background-position: 100% center;
          background-size: 100%;
          background-repeat: no-repeat;
        }
        &:hover {
          background-color: $bg-tertiary;
        }
        select {
          width: 30rem;
          z-index: 5;
          height: clamp(3.5rem, 4.6vh, 4.1rem);
          padding: clamp(0.5rem, 1.1vh, 1rem) 1.6rem clamp(0.5rem, 1.1vh, 1rem)
            4.2rem;
          background: transparent;
          color: $color-contrast;
          cursor: pointer;
          appearance: none;
          &:active {
            background: transparent;
          }
          &:focus {
            background: transparent;
          }
        }
        i {
          position: absolute;
          left: 0;
          top: 50%;
          transform: translate(0, -50%);
          color: $bg-primary;
          font-size: $font-size-icons;
          padding: clamp(0.5rem, 1vh, 0.9rem) 1.6rem;
        }
      }
      &__field {
        position: relative;
        display: flex;
        flex-direction: column;
        gap: 0.5rem;
        & > i {
          position: absolute;
          left: 0;
          top: 75%;
          transform: translate(0, -55%);
          color: $bg-primary;
          font-size: $font-size-icons;
          padding: clamp(0.5rem, 1vh, 0.9rem) 1.6rem;
        }
      }
      &__field__message {
        position: relative;
        display: flex;
        flex-direction: column;
        gap: 0.5rem;
        textarea {
          width: 30rem;
          height: clamp(8rem, 17.7vh, 16rem);
          padding: clamp(0.5rem, 1.1vh, 1rem);
          resize: none;
          background-color: $color-primary;
        }
      }
      &__submit {
        display: flex;
        justify-content: center;
        button {
          padding: clamp(0.4rem, 1.65vh, 1.5rem);
          width: 11rem;
          height: clamp(3.5rem, 5.5vh, 4.9rem);
          line-height: 100%;
          background-color: $bg-submit;
          transition: all 0.3s ease-in-out;
          text-transform: uppercase;
          font-weight: 600;
          &:hover {
            background-color: $color-primary;
            transform: scale(1.1);
            transition: all 0.3s ease-in-out;
          }
        }
      }
      //-----------Chat tawkTo-----------
      &__chatTawkTo {
        min-height: 44rem;
        padding: 2rem 0;
        & div {
          position: relative !important;
        }
      }
    }
    &__list {
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 2rem;
      margin-top: clamp(1.6rem, 2.3vh, 2rem);
      @include for-phone-only {
        margin-bottom: 0.7rem;
      }
      button {
        display: flex;

        svg {
          fill: $color-primary;
          transition: 0.3s ease-in-out;
          &:hover {
            fill: $bg-active;
            transform: scale(1.1);
            transition: 0.3s ease-in-out;
          }
        }
        .svg-active {
          fill: $bg-active;
        }
      }
    }
    &__tawkto {
      color: white;
    }
  }
}
.tawk-overlay {
  position: relative !important;
}
.disabled {
  display: none;
}
.widget-container {
  flex-direction: column;
}
