//-------------------NUMBER PHONE----------------------
.numberPhone {
  position: fixed;
  z-index: 10;
  bottom: 1vh;
  left: 1%;
  @include responsive-custom-max-width(621px) {
    left: 5%;
    bottom: 11vh;
  }
  p {
    line-height: 50%;
    font-family: $font-secondary, $font-default;
    font-size: map-get($font-size, h2);
  }
}
