//-------------------Menu----------------------
.navbar {
  position: absolute;
  z-index: 5;
  top: 50%;
  transform: translate(0, -50%);
  margin-left: 6rem;
  /*   @include responsive-custom-max-width(835px) {
    margin-left: clamp(2rem, 6.7vmin, 4rem);
  } */
  @include responsive-custom-max-width(1023px) {
    left: 50%;
    transform: translate(-50%, -50%);
    margin: 0;
    width: clamp(5rem, 66.9vw, 68.4rem);
    height: clamp(5rem, 34.42vw, 35.2rem);
  }
  height: 37.6rem;

  &:hover {
    & ~ .main__wrapper--blur {
      filter: blur(0);
      transition: all 0.5s ease-in-out;
    }
    /*    & ~ .blurFirefox {
      filter: blur(0);
      transition: all1s ease-in-out;
    } */
  }
  &__wrapper {
    &__active-container {
      display: flex;
      flex-direction: column;
      gap: 2rem;

      li:first-child {
        text-transform: uppercase;
      }
      li {
        span {
          display: inline-block;
          font-size: 8rem;
          @include responsive-custom-max-width(1023px) {
            font-size: map-get($font-size, big);
          }
          font-family: map-get($font-primary, bold), $font-default;
          white-space: nowrap;
        }

        p {
          max-width: 72.2rem;
          text-align: justify;
          animation: fadeInNav 1s ease-in-out forwards;
          opacity: 0;
          padding: 0.5rem;
          @include responsive-custom-max-width(1023px) {
            padding: 0.2rem;
          }
        }
      }
    }
    &__return-btn {
      margin-top: 10rem;
      text-align: right;
      animation: fadeInNav 1s ease-in-out forwards;
      opacity: 0;
      transition: 0.3s ease-in-out;
      @include responsive-custom-max-width(621px) {
        margin-top: 4rem;
      }
      a {
        display: inline-block;
        @include responsive-custom-max-width(621px) {
          margin-right: -0.6rem;
        }
      }
      button {
        @include responsive-custom-max-width(621px) {
          transform: scale(0.8);
        }
        svg {
          fill: #fff;
          stroke-width: 0.0798399;
          transition: all 0.3s ease-in-out;
          &:hover {
            fill: #cccccc;
            transform: scale(1.1);
            transition: all 0.3s ease-in-out;
          }
        }
      }
    }
    &__animations {
      display: flex;
      flex-direction: column;
      justify-content: center;
      a {
        display: inline-block;
        font-size: 8rem;
        @include responsive-custom-max-width(1023px) {
          font-size: map-get($font-size, big);
        }
        font-family: map-get($font-primary, bold), $font-default;
        text-transform: uppercase;
        white-space: nowrap;
        transition: all 0.3s ease-in-out;
        &:hover {
          color: #cccccc;
          transform: scale(1.05);
          transition: all 0.3s ease-in-out;
        }
      }
      // ---------------------COURT------------------------------
      .active-court {
        animation: fadeInUpNavCourt 1s ease-in-out forwards;
      }
      .active-courts {
        animation: fadeInDownNavCourts 1s ease-in-out forwards;
      }
      // ---------------------COURT REVERSE------------------------------
      .active-court-reverse {
        animation: fadeInUpNavCourt 1s ease-in-out forwards reverse;
      }
      .active-courts-reverse {
        animation: fadeInDownNavCourts 1s ease-in-out forwards reverse;
      }
      // ---------------------DOCS------------------------------
      .active-doc {
        animation: fadeInUpNavDoc 1s ease-in-out forwards;
      }
      .active-docs {
        animation: fadeInDownNavDocs 1s ease-in-out forwards;
      }
      // ---------------------DOCS REVERSE------------------------------
      .active-doc-reverse {
        animation: fadeInUpNavDoc 1s ease-in-out forwards reverse;
      }
      .active-docs-reverse {
        animation: fadeInDownNavDocs 1s ease-in-out forwards reverse;
      }
      // ---------------------CLIPS------------------------------
      .active-clip {
        animation: fadeInUpNavClip 1s ease-in-out forwards;
      }
      .active-clips {
        animation: fadeInUpNavClips 1s ease-in-out forwards;
      }
      // ---------------------CLIPS REVERSE------------------------------
      .active-clip-reverse {
        animation: fadeInUpNavClip 1s ease-in-out forwards reverse;
      }
      .active-clips-reverse {
        animation: fadeInUpNavClips 1s ease-in-out forwards reverse;
      }
      // ---------------------COMS------------------------------
      .active-com {
        animation: fadeInUpNavCom 1s ease-in-out forwards;
      }
      .active-coms {
        animation: fadeInUpNavComs 1s ease-in-out forwards;
      }
      .active-com-down {
        animation: fadeInDownNavCom 1s ease-in-out forwards;
      }
      // ---------------------COMS REVERSE------------------------------
      .active-com-reverse {
        animation: fadeInUpNavCom 1s ease-in-out forwards reverse;
      }
      .active-coms-reverse {
        animation: fadeInUpNavComs 1s ease-in-out forwards reverse;
      }
      .active-com-down-reverse {
        animation: fadeInDownNavCom 1s ease-in-out forwards reverse;
      }
    }
  }
  .active-anim {
    animation: fadeInNav 2s ease-in-out forwards;
    opacity: 0;
  }
}
