//-------------------Follow----------------------
.follow-container {
  position: fixed;
  z-index: 10;
  bottom: 1%;
  @include responsive-custom-min-width(map-get($breakpoints, phone)) {
  right: clamp(10rem, 7vw, 11rem);
  }
  display: flex;
  gap: 1.5rem;
@include responsive-custom-max-width(621px) {
  left: 5%;
}
  a {
    button {
      svg {
        fill: $color-primary;
        transition: all 0.3s ease-in-out;
        &:hover {
          fill: #cccccc;
          transform: scale(1.1);
          transition: all 0.3s ease-in-out;
        }
      }
    }
  }
}
