//------------------Grid layout ---------------------

.container {
  /*   display: grid;
  grid-template-areas:
  "header header header"
  "main main main"
  "footer footer footer"; */
  width: 100%;
  height: 100%;
  margin: 0 auto;
  overflow: hidden;
  &__phone-direction {
    position: absolute;
    display: flex;
    z-index: 50;
    top: 10%;
    left: 50%;
    transform: translate(-50%, 0);
    opacity: 0;
    animation: opacityPhone 8s ease-in-out forwards;
  }
  &__return-btn{
    position: absolute;
    display: flex;
    z-index: 50;
    bottom: 2vh;
    right: 2vw;
          button {
        svg {
          fill: #fff;
          stroke-width: 0.0798399;
          transition: all 0.3s ease-in-out;
          &:hover {
            fill: #cccccc;
            transform: scale(1.1);
            transition: all 0.3s ease-in-out;
          }
        }
      }
  }
}
//-------------------Animations opacity phone-------------------
@keyframes opacityPhone {
  0% {
  }
  10% {
    opacity: 1;
  }
  20% {
    opacity: 0;
  }
  30% {
    opacity: 1;
  }
  40% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  60% {
    opacity: 0;
  }
  70% {
    opacity: 1;
  }
  80% {
    opacity: 0;
  }
  90% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
