//------------------Header ---------------------
.header {
  position: relative;
  display: flex;
  gap: 6rem;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 0 clamp(1rem, 6.3%, 6rem);
  z-index: 6;
  background-color: $bg-secondary;
  color: $color-secondary;
  @include responsive-custom-max-width(1098px) {
    justify-content: center;
  }
  /*  h1, h2 {
    margin-top: 1rem;
  } */
  h1 {
    font-size: 5rem;
    min-width: 46.9rem;
    font-weight: 500;
    color: $color-secondary;
    transition: 0.3s ease-in-out;
   
    @include responsive-custom-max-width(621px) {
      font-size: map-get($font-size, h1);
      text-align: center;
    }

    &:hover {
      color: $color-primary;
    }
  }
  h2 {
    @include responsive-custom-max-width(1338px) {
      max-width: 46.7rem;
    }
    @include responsive-custom-max-width(1116px) {
      max-width: 43rem;
    }
    @include responsive-custom-max-width(1098px) {
      display: none;
    }
    @include for-tablet-portrait-up {
      max-width: 23.2rem;
    }
    font-size: map-get($font-size, h2);
    font-weight: 500;
    color: $color-secondary;
    transition: 0.3s ease-in-out;
  }
}
