//-------------------VideoCine----------------------
//Main
.main {
  overflow: hidden;
  width: 100%;
  height: 100%;
  &__wrapper {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    width: 100%;
    height: 100%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    //format ratio 19:9
    // padding: 56.25% 0 0 0;
    //format ratio  3:2
    // padding-top: 66.66%;
    &__cover {
      transition: all 0.5s ease-in-out;
      @include responsive-custom-max-width(621px) {
        position: relative;
        overflow: hidden;
        width: 100vw;
        height: 100vh;
        object-fit: cover;
      }
    }
    &--blur {
      position: absolute;
      overflow: hidden;
      width: 100%;
      height: 100%;
      z-index: 3;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      filter: blur(0.5rem);
      transition: all 0.5s ease-in-out;
      /* clip-path: content-box; */
      /* backdrop-filter: blur(0.5rem); */
      @include responsive-custom-max-width(1024px) {
        filter: blur(0);
      }
    }
  }
  iframe {
    background-color: $bg-primary;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100vw;
    height: 56.25vw;
    min-width: 100vw;
    min-height: 100vh;
    pointer-events: none;
    /*  @include responsive-custom-max-width(621px) {
        width: 300%;
        height: 300%;
      } */
    @include responsive-custom-max-width(621px) {
      width: 550vw;
      height: 300vh;
      /*   width: 1920px;
       height: 1080px; */
    }
  }
  &__link-to-subHome {
    display: block;
    position: absolute;
    z-index: 200;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 80%;
    height: 80%;
    border: 0.1rem dashed rgba($color: #cccccc, $alpha: 1);
    opacity: 0;
    transition: all 0.5s ease-in-out;
    &:hover {
      opacity: 1;
      transition: all 0.5s ease-in-out;
      & + .main__wrapper > .main__wrapper__cover {
        transform: scale(1.05);
        transition: all 0.5s ease-in-out;
      }
    }
  }
}
